import { Directive, Input, OnInit, Optional, Self, Injector } from '@angular/core';
import { NgControl } from '@angular/forms';
import { ListFilterComponent } from '../list-filter.component';

@Directive({
    selector: '[clear-fields]'
})
export class ClearFieldsDirective implements OnInit {
    @Input('clear-fields') clearFields = '';

    constructor(@Optional() @Self() private ngCtrl: NgControl, private listFilterCmp: ListFilterComponent, private inject: Injector) { }

    ngOnInit() {
        if (this.clearFields && this.ngCtrl) {
            this.ngCtrl.valueChanges.subscribe((v) => {
                this.listFilterCmp.fieldCodeOfClearFields = [];
                this.clearFields.split(',').forEach(n => {
                    // å­æ®µç¼å·
                    const conditionItem = this.listFilterCmp.listFilterConditions.find(c => c.fieldCode === n);
                    if (conditionItem) {
                        // è§¦åæ¸ç©ºå­æ®µåè¡¨åæ´ï¼ç¹æ®æ§ä»¶ç±»åçæ¸ç©ºï¼ä¸è½åªæ¯æ¸ç©ºå¼
                        this.listFilterCmp.fieldCodeOfClearFields.push(n);
                        // æ¸ç©ºå­æ®µ
                        conditionItem.value.clearValue();
                    }
                });
            });
        }
    }
}
